import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import TimelineIcon from '@material-ui/icons/Timeline';
import BarChartIcon from '@material-ui/icons/BarChart';

import './StartPage.css';
import { startPageStyles } from './StartPage.style';

import { Link } from "react-router-dom";


class StartPage extends Component {
  render() {
    const { classes } = this.props;
    return (
        <Grid container spacing={16} className={classes.root}>
          <Grid item xs={12}>
            <Typography variant="title" align="center" gutterBottom> Welcome to miRQuest 2</Typography>
          </Grid>
          
          <Grid item xs={12} className={classes.homeOptionsGridContainer}>
            <Grid item xs={12} sm={6} className={classes.homeOptionGrid}>
              <Paper className={classes.paperContainer}>
                <Button className="button-home-page" component={Link} to="/mirquest2/process">
                  <TimelineIcon />
                  Process your data
                </Button>
              </Paper>
            </Grid>
            <Grid item xs={12} sm={6} className={classes.homeOptionGrid}>
              <Paper className={classes.paperContainer}>
                <Button className="button-home-page" component={Link} to="/mirquest2/report">
                  <BarChartIcon />
                  Check a report
                </Button>
              </Paper>
            </Grid>
          </Grid>
        </Grid>
    );
  }
}

StartPage.propTypes = {
  classes: PropTypes.object.isRequired,
  files: PropTypes.array,
  updateFiles: PropTypes.func,
};

export default withStyles(startPageStyles)(StartPage);



