export const confirmStepStyles = theme => ({
  infoTitle: {
    textAlign: 'center',
    paddingLeft: 10,
    paddingRight: 10,
    paddingTop: 10,
  },
  fullHeight: {
    height: '100%'
  },
  userForm: {
    alignItems: "center",
    display: "flex",
    flexDirection: "column",
    padding: 15,
    width: '100%'
  },
  submitBtn: {
    margin: theme.spacing.unit,
    marginTop: 15,
    maxWidth: 300
  },
  leftIcon: {
    marginRight: theme.spacing.unit,
  },
  rightIcon: {
    marginLeft: theme.spacing.unit,
  },
  textField: {
    margin: theme.spacing.unit,
  },
  stepTitle: {
    margin: "0 0 20px 0"
  }
});


